import React, { FunctionComponent, useCallback, useEffect, useState } from 'react';

type DataContextProviderProps = {
    children: React.ReactElement;
};

export type DataContextStoreEntity<Path extends string, DataType extends {} = {}> = {
    path: Path,
    data: DataType
}


type DataContextType = {
    store: {
        [key in string]: DataContextStoreEntity<key>
    },
    controller: {
        updateData: (params: DataContextStoreEntity<string>) => void;
    }
}

export const DataContext = React.createContext<DataContextType>({
    store: {},
    controller: {
        updateData: () => {}
    }
});

const DataContextProvider: FunctionComponent<DataContextProviderProps> = ({ children }) => {
    const [dataState, setDataState] = useState<DataContextType['store']>({});

    const updateData = useCallback((params: DataContextStoreEntity<string>) => {
        setDataState(p => ({
            ...p,
            [params.path]: params
        }));
    }, []);

    return <DataContext.Provider value={{
        store: dataState,
        controller: {
            updateData
        }
    }} >
        { children }
    </DataContext.Provider>;
}

export default DataContextProvider;