import React, { ChangeEvent, FunctionComponent, useCallback, useContext, useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { useCharts } from '../API/useCharts';
import { UserLoggerContext } from './UserLogger/UserLoggerContext';
import { useScrollToRight } from './useEffects/useScrollToRight';

type ChartDataProps = {
    data: number[];
    update: (data: number[]) => void;
};

const DataLayoutStyled = styled.div`
   background: #444444;
   border-radius: 10px;
   display: flex;
   color: white;
   gap: 7px;
   padding: 10px;
   overflow-x: auto;
   padding-right: 3rem;
   
   & + & {
      margin-top: 10px;
   }

   &::-webkit-scrollbar{
      display: none;
   }
`;

const DataElemStyled = styled.span`
   position: relative;
   padding-right: 7px;
   font-size: 16px;
   
   &:after {
      content: '';
      height: 100%;
      width: 1px;
      background-color: white;
      position: absolute;
      right: 0;
      top: 0;
   }
`;

const DataInput = styled.input`
   font-size: 16px;
   outline: none;
   border: none;
   background: transparent;
   width: 2.5rem;

   position: relative;
   padding-right: 7px;
   
   color: white;

   border-right: white solid 1px;
   
   &:after {
      content: '';
      height: 100%;
      width: 1px;
      background-color: white;
      position: absolute;
      right: 0;
      top: 0;
   }
`;

const ChartData: FunctionComponent<ChartDataProps> = (props) => {
    const { data, update } = props;
    const [activeIndex, setActiveIndex] = useState<number | undefined>(undefined);
    const editingValRef = useRef<number | undefined>(undefined);
    const { pushLog } = useContext(UserLoggerContext);
    const DataLayoutStyledRef = useRef<HTMLDivElement>(null);

    const finishEditing = useCallback(() => {
        if (editingValRef.current && activeIndex) {
            const newData = [...data];
            newData[activeIndex as number] = editingValRef.current;
            update(newData);
            pushLog({
                type: 'info',
                msg: `New val: {${editingValRef.current}}`
            });
        }

        editingValRef.current = undefined;
        setActiveIndex(undefined);
    }, [data, activeIndex]);

    const handleOutsideClick = useCallback((e: MouseEvent) => {
        if (!(e.target as any)?.closest('[data-tag="data-layout"]') && activeIndex) {
            finishEditing();
        }
    }, [finishEditing, activeIndex]);

    const handleOutsideClickRef = useRef(handleOutsideClick);

    useEffect(() => {
        handleOutsideClickRef.current = handleOutsideClick;
    }, [handleOutsideClick]);

    const handleEditing = useCallback((e: ChangeEvent<HTMLInputElement>) => {
        editingValRef.current = parseInt(e.target.value);
    }, []);

    useEffect(() => {
        const handleBodyClick = (e: MouseEvent) => {
            handleOutsideClickRef.current(e);
        };

        document.body.addEventListener('click', handleBodyClick);

        return () => document.body.removeEventListener('click', handleBodyClick);
    }, []);

    useScrollToRight(DataLayoutStyledRef);

    const handleInputKeyDown = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter') {
            finishEditing();
        }
    }, [finishEditing]);

    return <DataLayoutStyled ref={DataLayoutStyledRef} data-tag={'data-layout'} >
        {
            data.map((d, i) => (
                activeIndex === i ? (
                    <DataInput
                        onKeyDown={handleInputKeyDown}
                        data-tag={'data-layout'}
                        onChange={handleEditing}
                        type={'number'}
                        defaultValue={d}
                        autoFocus={true}
                    />
                ) : (
                    <DataElemStyled
                        data-tag={'data-layout'}
                        key={'' + d + i}
                        onClick={() => setActiveIndex(i)}
                    >{ d }</DataElemStyled>
                )
            ))
        }
    </DataLayoutStyled>;
}

export default ChartData;