import React, { FunctionComponent, useEffect, useState } from 'react';
import ChartPreview, { ChartPreviewProps } from './ChartPreview/ChartPreview';
import { getCharts, useWrapContextRequest } from '../API/api';
import Loader from './Loader';
import { useCharts } from '../API/useCharts';
import Layout from './Layouts/Layout';

type ChartsListProps = {

};


const ChartsList: FunctionComponent<ChartsListProps> = (props) => {
    const {
        isLoading,
        data: chartData
    } = useCharts();

    return <Layout>
        <Loader show={isLoading} />
        { chartData && chartData.map((data) => (
            <ChartPreview
                {...(data as unknown as ChartPreviewProps)}
            />
        )) }
    </Layout>;
}

export default ChartsList;