import staticData from '../mocks/chartsData.json';
import { useCallback, useContext, useEffect, useState } from 'react';
import { DataContext, DataContextStoreEntity } from '../Components/DataContext/DatacontextProvider';

interface IFakeAPIParams {
    cb: () => unknown;
    timer: number;
}

const makeFakeAPI = (params: IFakeAPIParams) => {
    return new Promise((resolve) => {
        setTimeout(() => resolve(params.cb()), params.timer);
    });
}

interface WrapContextRequestParams<P extends string, D extends {}> {
    path: P,
    requestCb: () => Promise<any>
}


// TODO: Integrate react query
export function useWrapContextRequest<
    DataType extends {} = {},
    Path extends string = string
>(params: WrapContextRequestParams<Path, DataType>) {
    const [isLoading, setIsLoading] = useState(false);

    const {
        store,
        controller
    } = useContext(DataContext);

    useEffect(() => {
        if (!store[params.path]) {
            setIsLoading(true);
            (async () => {
                controller.updateData({
                    path: params.path,
                    data: await params.requestCb()
                });

                setIsLoading(false);
            })();
        }
    }, []);

    const updateData = useCallback((path: string, data: DataType) => {
        controller.updateData({
            path,
            data
        });
    }, [
        controller.updateData,
        store
    ]);

    return {
        isLoading,
        data: store[params.path]?.data as DataType,
        updateData
    }
}


// TODO: Implement real data fetching
export async function getCharts() {
    return await makeFakeAPI({
        timer: 1000,
        cb: () => staticData
    });
}