import React, { createContext, FunctionComponent, useCallback, useEffect, useState } from 'react';
import { createPortal } from 'react-dom';
import styled, { keyframes } from 'styled-components';

type LogType = {
    msg: string;
    type: 'warning' | 'error' | 'info'
}

type UserLoggerContext = {
    pushLog: (log: LogType) => void;
}

export const UserLoggerContext = createContext<UserLoggerContext>({
    pushLog: () => { console.error('Context didn\'t load') }
});

const fadeInAnim = keyframes`
   from {
      transform: translateY(-10px);
      opacity: 0;
   }
   
   to {
      transform: translateY(0);
      opacity: 1;
   }
`;

const fadeOutAnim = keyframes`
   from {
      transform: translateY(0);
      opacity: 1;
   }
   
   to {
      transform: translateY(10px);
      opacity: 0;
   }
`;

const LogElementStyled = styled.div<{ type: LogType['type'] }>`
   z-index: 9999;
   position: absolute;
   display: inline-block;
   top: 10px;
   right: 20px;
   color: white;
   padding: 5px 10px;
   border-radius: 5px;
   box-shadow: 0 0 50px #3a3a3a55;
   
   background: ${
        ({ type }) => {
            let color: string;
            
            switch (type) {
               case 'info':
                   color = '#3dc9ff';
                   break;
               case 'error':
                   color = 'red';
                   break;
               case 'warning':
                   color = 'orange';
            }
            
            return color;
        }
   };
   
   animation: ${fadeInAnim} .25s ease-in 1;
   animation-fill-mode: forwards;
   
   &.fading-out {
      animation-name: ${fadeOutAnim};
   }
`;



type LogElementProps = {
    log: LogType;
}

const LogElement: FunctionComponent<LogElementProps> = ({ log }) => {
    const [isFadingOut, setIsFadingOut] = useState(false);

    useEffect(() => {
        const timeOutCode = setTimeout(() => {
            setIsFadingOut(true);
        }, 2750);

        return () => clearTimeout(timeOutCode);
    }, []);

    return createPortal((
        <LogElementStyled
            type={log.type}
            className={isFadingOut ? 'fading-out' : ''}
        >{ log.msg }</LogElementStyled>
    ), document.querySelector('.portal')!);
};

type UserLoggerContextProviderProps = {
    children: React.ReactElement;
};

const UserLoggerContextProvider: FunctionComponent<UserLoggerContextProviderProps> = ({ children }) => {
    const [activeLog, setActiveLog] = useState<LogType | null>(null);

    const pushLog = useCallback((log: LogType) => {
        setActiveLog(log);
    }, []);

    useEffect(() => {
        if (activeLog) {
            setTimeout(() => {
                setActiveLog(null);
            }, 3000);
        }
    }, [activeLog]);

    return <UserLoggerContext.Provider value={{ pushLog }} >
        { activeLog && (
            <LogElement log={activeLog} />
        )}
        { children }
    </UserLoggerContext.Provider>;
}

export default UserLoggerContextProvider;