import React, { FunctionComponent, useCallback, useEffect } from 'react';
import { ChartDataType } from '../../Models/Chart';
import { ChartPreviewStyled } from './ChartPreviewStyled';
import {
    useNavigate
} from 'react-router-dom';
import Chart from '../Chart';

export type ChartPreviewProps = {
    id: string;
    chartData: ChartDataType<any>;
    label: string;
};

const ChartPreview: FunctionComponent<ChartPreviewProps> = (props) => {
    const {
        id,
        chartData,
        label
    } = props;

    const navigate = useNavigate();

    const openPage = useCallback(() => {
        navigate(`/chart/${id}`);
    }, []);

    return (
        <div onClick={openPage}>
            <h3 style={{ textAlign: 'left', color: '#3a3a3a' }} >{ label }</h3>
            <ChartPreviewStyled>
                <Chart
                    data={chartData}
                />
            </ChartPreviewStyled>
        </div>
    );
}

export default ChartPreview;