import { getCharts, useWrapContextRequest } from './api';
import { ChartPreviewProps } from '../Components/ChartPreview/ChartPreview';
import { useCallback } from 'react';

type ChartUpdateOptions = {
    id: string,
    dataSetIndex: number;
    newDataSetValues: number[];
};

export const useCharts = () => {
    const {
        isLoading,
        data,
        updateData
    } = useWrapContextRequest<ChartPreviewProps[]>({
        path: '/charts',
        requestCb: getCharts
    });

    const update = useCallback(({
        id,
        dataSetIndex,
        newDataSetValues
    }: ChartUpdateOptions) => {
        const newData = data.map(chartData => {
            if (chartData.id !== id) {
                return chartData;
            }

            const newChartData = JSON.parse(JSON.stringify(chartData));

            newChartData.chartData.dataSets = chartData.chartData.dataSets.map((dataSet, index) => {
                if (index !== dataSetIndex) {
                    return dataSet;
                }

                const newDataSet = JSON.parse(JSON.stringify(dataSet));

                newDataSet.data = newDataSetValues;

                return newDataSet;
            });

            return newChartData;
        });

        updateData('/charts', newData);
    }, [
        updateData,
        data
    ]);

    return {
        isLoading,
        data,
        update
    }
}