import React, { FunctionComponent } from 'react';
import { useParams } from 'react-router-dom';
import Chart from '../Chart';
import { useCharts } from '../../API/useCharts';
import Loader from '../Loader';
import { ChartPreviewProps } from '../ChartPreview/ChartPreview';
import Layout from '../Layouts/Layout';
import styled, { CSSProperties } from 'styled-components';
import ChartData from '../ChartData';

type ChartPageProps = {

};

const ChartPanelStyled =  styled.div`

`;

const chartPageLayoutStyles: CSSProperties = {
    height: '100vh',
    background: 'linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, #FFFFFF 20%, rgba(255, 255, 255, 0) 100%)'
}

const ChartPageChartDataWrapper = styled.div`
   //padding-top: 50px;
`;

const ChartPage: FunctionComponent<ChartPageProps> = (props) => {
    const { id } = useParams();
    const {
        data: chartsData,
        isLoading,
        update
    } = useCharts();

    const chartData: ChartPreviewProps | undefined =
        chartsData ? chartsData.find(CD => CD.id === id) : undefined;

    const updateDataSet = (dataSetIndex: number, data: number[]) => {
        if (!chartData) {
            return;
        }

        update({
            id: chartData.id,
            dataSetIndex,
            newDataSetValues: data
        });
    }

    return (
        <Layout style={chartPageLayoutStyles} >
            <Loader show={isLoading} />
            { chartData ? (
                <ChartPanelStyled>
                    <h1>Chart: [{ chartData.label }]</h1>
                    <Chart
                        data={chartData.chartData}
                    />
                    <ChartPageChartDataWrapper>
                        { chartData.chartData.dataSets.map((dataSet, i) => (
                            <ChartData
                                data={Object.entries(dataSet.data).map(([_, v]) => v)}
                                update={(data: number[]) => updateDataSet(i, data)}
                            />
                        )) }
                    </ChartPageChartDataWrapper>
                </ChartPanelStyled>
            ) : null }
        </Layout>
    );
}

export default ChartPage; 